body{font-family:-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,sans-serif;background:#202124}header{margin-top:2rem;text-align:center}header h2{font-size:3rem;font-weight:400;color:#ffcb05}.content{margin-top:2rem;padding:1rem;text-align:center;background:linear-gradient(to bottom,rgba(49,52,61,.8),rgba(44,62,80,.808)) fixed;border-radius:6px;-webkit-border-radius:6px;-moz-border-radius:6px;-ms-border-radius:6px;-o-border-radius:6px;box-shadow:3px 3px 20px #ffcb05}.content h1{font-size:8rem;font-weight:700;letter-spacing:2px;color:#ffcb05}.content h1 .animate{animation:animate 4s infinite;-webkit-animation:animate 4s infinite}@keyframes animate{0%{color:#ff5722;font-size:2rem}40%{color:#ffab91;font-size:4rem}80%{color:#f4511e;font-size:6rem}100%{color:#ffcb05;font-size:8rem}}.content h2{font-size:1.4rem;font-weight:300;color:#ffcb05}.input-group{margin:2rem auto;width:100%;max-width:400px}.input-group #search-btn{background-color:#ffcb05;transition:all .4s;-webkit-transition:all .4s;-moz-transition:all .4s;-ms-transition:all .4s;-o-transition:all .4s}.input-group #search-btn:hover{background-color:#f0800a}.link a{padding:0 .6rem;font-size:1rem;color:#ffcb05;transition:all .4s;-webkit-transition:all .4s;-moz-transition:all .4s;-ms-transition:all .4s;-o-transition:all .4s}.link a:hover{color:#f0800a}.link a i{padding-right:.3rem}.social{padding:.8rem}.social h3{position:relative;width:100px;font-size:1.2rem;font-weight:400;letter-spacing:2px;animation:follow 3s infinite;-webkit-animation:follow 3s infinite}.social ul{margin-top:.8rem;padding:0;list-style-type:none}.social ul li{display:inline-block;padding-left:.8rem;font-size:2rem}.social ul li:nth-child(1){padding-left:0}.social ul li a{color:#ffcb05}.social ul li a:hover{color:#f0800a}.social ul li a i:hover{transition:all .4s;-webkit-transition:all .4s;-moz-transition:all .4s;-ms-transition:all .4s;-o-transition:all .4s;box-shadow:3px 3px 5px #ffcb05}@keyframes follow{0%{color:#ff5722;left:80px}50%{color:#ffab91;left:60%}100%{color:#ffcb05;left:80px}}footer{margin-top:2rem;text-align:center}footer p{color:#ffcb05}footer a{color:#ffcb05;transition:all .4s;-webkit-transition:all .4s;-moz-transition:all .4s;-ms-transition:all .4s;-o-transition:all .4s}footer a:hover{text-decoration:none;color:#f0800a}@media (max-width:767.98px){header{margin-top:.8rem}header h2{font-size:2rem}.content{padding:0 .4rem}.content h2{font-size:1.2rem}@keyframes follow{0%{color:#ff5722;left:40px}50%{color:#ffab91;left:60%}100%{color:#ffcb05;left:40px}}footer{margin-top:.6rem}}